<script>
    import { onMount } from 'svelte';
    import { geoPath, geoMercator } from 'd3-geo';
    import { csvParse } from 'd3-dsv';
  
    let geojson;
    let pathGenerator;
    let eqData = {};
    let redRegions = [];
    let yellowRegions = [];
    let isEnd = false;

    let curYear = 2024;
    let curMonth = 12;
    let sliderValue = 119;

    function increase(){
        if(sliderValue < 119){
            sliderValue++;
            updateDate();
        }
    }

    function decrease(){
        if(sliderValue > 0){
            sliderValue--;
            updateDate();
        }
    }


    $: {
        curYear = Math.floor(sliderValue / 12) + 2015;
        curMonth = sliderValue % 12 + 1;
    }

    function getColor(count) {
        if (!count) return 'none';
        if (count > 8) return '#ff0000';   // 진한 빨강
        if (count > 4) return '#ff9999';   // 연한 빨강
        return 'none'; // 4회 미만은 칠하지 않음
    }

    async function loadGeoData(){
        const res = await fetch('assets/diss.geojson');
        geojson = await res.json();
    }
    
    async function calcEQData(){
        const eqRes = await fetch('assets/EQdata.csv');
        const eqText = await eqRes.text();
        const eqParsed = csvParse(eqText);

        const startDate = new Date(curYear, curMonth - 13);
        const endDate = new Date(curYear, curMonth-1);

        const counts = {};
        for (const row of eqParsed) {
            const dateStr = row['발생시각'];
            if (!dateStr) continue; // 안전장치
            const dateParts = dateStr.split(' ')[0].split('.');
            if (dateParts.length < 3) continue;

            const year = parseInt(dateParts[0]);
            const month = parseInt(dateParts[1]);
            const day = parseInt(dateParts[2]);
            const eventDate = new Date(year, month - 1, day);
            if(startDate <= eventDate && eventDate < endDate) {
                const region = row['위치'];
                counts[region] = (counts[region] || 0) + 1;
            }
        }
        eqData = counts;
        console.log(eqData);

        redRegions = [];
        yellowRegions = [];
        for(const region in eqData) {
            if(eqData[region] > 8) {
                redRegions.push(region);
            } else if(eqData[region] > 4) {
                yellowRegions.push(region);
            }
        }
        isEnd = true;
    }

    function updateDate(){
        isEnd = false;
        calcEQData();
    }

    onMount(async () => {  
        await loadGeoData();
        await calcEQData();

        const projection = geoMercator().center([131.3, 36]).scale(4000);
        pathGenerator = geoPath().projection(projection);
    });    
</script>

<div class="controls">
    <div class="slider-container">
        <button on:click={decrease}>◀</button>
        <label>
            <input
                type="range"
                bind:value={sliderValue}
                min=0
                max=119
                on:input = {updateDate}
            />
        </label>
        <button on:click={increase}>▶</button>
    </div>
    <div>
        <p>{curYear}년 {curMonth}월</p>
    </div>
</div>

  

<div class = "container">
    {#if isEnd}
        <svg
            viewBox="0 0 550 550"
            preserveAspectRatio="xMidYMid meet"
            class="map">
            {#if geojson && pathGenerator}
                {#each geojson.features as feature}
                <path 
                    d={pathGenerator(feature)} 
                    fill={getColor(eqData[feature.properties.Full_Name])}
                    stroke="#000" 
                    stroke-width="0.5"
                />
                {/each}
                {#each geojson.features as feature}
                {#if eqData[feature.properties.Full_Name] > 4}
                    <text
                        x={pathGenerator.centroid(feature)[0]}
                        y={pathGenerator.centroid(feature)[1]}
                        font-size="10"
                        fill="black"
                        text-anchor= "middle"
                        font-weight="bold"
                    >
                        {feature.properties.Full_Name.split(' ').pop()}
                    </text>
                {/if}
                {/each}
            {/if}
        </svg>
        
        <div class="lists">
            <div class="regions">
                <h3>매우 활성 지역</h3>
                <ul>
                    {#each redRegions.sort() as region}
                        <li>{region}</li>
                    {/each}
                </ul>
            </div>
            <div class="other-regions">
                <h3>활성 지역</h3>
                <ul>
                    {#each yellowRegions.sort() as region}
                        <li>{region}</li>
                    {/each}
                </ul>
            </div>
        </div>
    {:else}
        <div style="align-self: center;">
            Loading... 모바일 환경에서는 로딩이 더 길어질수도 있습니다.
        </div>
    {/if}
</div>

<div class="description">
    <p>3개월 평균 미소지진이란 최근 1년간 발생한 미소지진 횟수를 4분기로 나눈 값으로,</p>
    <p>해당 값이 2를 초과할시(1년동안 9번 이상 지진 발생) '매우 활성'으로,</p>
    <p>1초과 2이하일 시(1년동안 5번 이상, 8번 이하 지진 발생) '활성'으로 표시</p>
</div>

